<template>
  <div class="featured-container">
    <h3>Find the Best Happy Hours in Town</h3>
    <div class="featured_restaurants">
      <div class="featured_restaurant"
           v-for="restaurant in restaurants"
           v-bind:key="restaurant._id">
        <img src="../assets/martini_placeholder.png">
        <p>
        {{restaurant.name}}
        </p>
      </div>
    </div>
  </div>

</template>

<script>
    import RestaurantService from "../RestaurantService";
    export default {
        name: "Featured",
        data() {
            return {
                restaurants: [],
            }
        },
        async created() {
            try {
                let restaurants = await RestaurantService.getRestaurants();
                this.restaurants = restaurants.slice(0, 4);
            } catch (err) {
                this.error = err.message;
            }
        },

    }
</script>

